// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-deep-sky-blue:               #00b2ff;
$color-deep-sky-blue-2:             #00b3ff;
$color-torch-red:                   #ff1553;
$color-alice-blue:                  #f2f5f6;
$color-alice-blue-2:                #f8fdff;
$color-black-pearl:                 #041827;
$color-black-pearl-2:               #041427;
$color-azure:                       #fafeff;
$color-whisper:                     #ebebeb;
$color-swamp:                       #04142726;
$color-cerulean:                    #026693;
$modal-shadow:                      #03101bb3;
$color-pacific-blue:                #008ecc;
$color-rhino:                       #364352;
$color-alice-blue-opacity:          #f2f5f780;
$color-vermilion:                   #ff4d00;
$color-apricot:                     #ffc5ac;
$color-dimgrey:                     #696969;
$color-linen:                       #faf0e6;

// Gradient

$black-to-right:                    linear-gradient(90deg, #404040 0%, #000000 75%);
$header-background:                 linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
$intro-background:                  linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%);


// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-placeholder:                    "Placeholder", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-770:                            770px;
$vp-1024:                           1024px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;


// Fonts
// ---------------------------------

$font-robotocondensed: "Roboto Condensed", "Arial", sans-serif;
$font-roboto: "Roboto", "Arial", sans-serif;
$font-ptmono: "PT Mono", "Arial", sans-serif;
