.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background: $modal-shadow;

  @include vp-1023 {
    display: none;
  }
}

.modal__inner {
  position: fixed;
  width: 500px;
  background-color: $color-deep-sky-blue-2;
  border-radius: 3px;
  padding: 38px 40px 45px;
  top: 146px;
  left: 702px;
  z-index: 10000;
}

.modal__title {
  font-family: $font-robotocondensed;
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  color: $color-default-white;
  margin: 0 0 4px;
}

.modal__description {
  font-family: $font-ptmono;
  font-size: 16px;
  line-height: 22px;
  color: $color-default-white;
  margin: 0 0 37px;
  width: 380px;
}

.modal__form {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.modal__user {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.modal__field input {
  width: 100%;
  background: $color-swamp;
  border-radius: 3px;
  border: none;
  color: $color-default-white;
  height: 50px;
  padding: 15px;
  font-family: $font-ptmono;
  font-size: 16px;
  line-height: 22px;

  &::placeholder {
    font-family: $font-ptmono;
    font-size: 16px;
    line-height: 20px;
    color: $color-default-white;
  }
}

.modal__field textarea {
  width: 100%;
  height: 155px;
  margin: 0;
  background: $color-swamp;
  border-radius: 3px;
  border: none;
  padding: 22px 15px;
  font-family: $font-ptmono;
  font-size: 16px;
  line-height: 20px;
  color: $color-default-white;

  &::placeholder {
    font-family: $font-ptmono;
    font-size: 16px;
    line-height: 22px;
    color: $color-default-white;
  }
}

.modal__field {
  &--button {
    background: $color-black-pearl-2;
  }

  &--button input {
    font-family: $font-robotocondensed;
    font-weight: 700;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    height: 45px;
    cursor: pointer;

    @include hover-focus {
      background-color: $color-rhino;

      &:active {
        background-color: $color-alice-blue;
        color: $color-black-pearl-2;
      }
    }

    &:disabled {
      background-color: $color-dimgrey;
      color: $color-linen;
      border-color: $color-linen;
      opacity: 1;
      cursor: auto;

      @include hover-focus {
        border-color: $color-linen;
        color: $color-linen;
        background-color: $color-dimgrey;

        &:active {
          border-color: $color-linen;
          color: $color-linen;
          background-color: $color-dimgrey;
        }
      }
    }
  }

  &--checkbox {
    display: flex;
    gap: 10px;
    margin: 0 9px;
    cursor: pointer;
  }
}

.modal__end {
  display: flex;
  gap: 20px;
  flex-direction: column;
}

.modal__checkbox {
  position: relative;
  border: 1px solid $color-default-white;
  border-radius: 2px;
  width: 15px;
  height: 15px;

  @include hover-focus {
    border-color: $color-apricot;

    &:active {
      border-color: $color-vermilion;
    }

    & .modal__checkbox-icon {
      color: $color-apricot;

      &:active {
        color: $color-vermilion;
      }
    }
  }
}

.modal__checkbox-icon {
  position: absolute;
  top: 3px;
  left: 2px;
  color: $color-default-white;
}

.modal__checkbox-text {
  font-family: $font-ptmono;
  font-size: 14px;
  line-height: 16px;
  color: $color-default-white;
  margin: 0;
  opacity: 0.8;
  margin-bottom: -4px;
}

.modal__button {
  position: absolute;
  top: 15px;
  right: 15px;
  border: none;
  background-color: $color-deep-sky-blue;
  padding: 10px;
  cursor: pointer;

  @include hover-focus {
    & .modal__button-icon {
      color: $color-rhino;

      &:active {
        color: $color-black-pearl-2;
      }
    }
  }
}

.modal__button-icon {
  color: $color-default-white;
}
